p {
  margin: 20px auto;
}

button {
  width: 200px;
  height: 50px;
  cursor: pointer;
  color: #fff;
  border: none;
  border-radius: 15px;
  outline: none;
  margin: 40px auto;
  font-size: 40px;
  display: block;
}

.start {
  background-color: #7cfc00;
}

.stop {
  background-color: red;
}

.start:disabled {
  background-color: gray;
}

